	module.exports = {"data":{"markdownRemark":{"html":"<p>ジン・ヨンの小説「シンデレラの英雄の伝説」を読むと、シンガポールで彼と出会い、とても親切な人です。私はそれを前に見ましたが、私はすでに感銘を受けています。インスピレーションは、その賢いアイデアと大胆な想像力です。架空のものであるにもかかわらず、この想像力は科学研究に携わる人にとって価値があります。創造的であるために、気まずい、元の気絶、突然の出現の出現を作る方法。いくつかの大きな科学者は、学生を訓練するとき、最初に彼らに武道の小説を見せたいと言われています。</p>\n<p>大学院のスタッフは、国際関係における重要な分野の構築と \"211プロジェクト\"のための戦いについて話し合うためのフォーラムを開くことになった。 211プロジェクトは国家教育委員会が提案した計画であり、21世紀には世界レベルの100の大学が設立され、大学はそれに向けて戦っています。</p>\n<p>私たちはその部門のアイデアを報告しました。彼らの意見は、一連の学問分野を確立することであり、我々は世界経済と国際金融と一緒に一連の学問分野を形成したいと考えています。私たちは主要な分野で努力しなければならない政治学の専攻について話しました。私たちが最後に戦う理由はおそらくありませんでした。学校のさまざまな分野には多くの問題があり、その多くは人間の問題であり、調整されていません。学校が長くなると、これが起こります。これらの問題を真っ直ぐにすることができなければ、大きな発展の可能性はない。</p>\n<p>大学院の専門家と指導者は、新しい方向を設定し、新たな成長点を探す必要があります。政治学では、政治理論、政治経済、政治発展、行政開発、公共政策などを確立し、国際関係と国際政治に国際関係理論、国際統合、世界問題、国際政治経済を確立したいそうすれば、開発のための新たな出発点が生まれます。開発する新しい分野もいくつかあります。</p>\n<p>「ライオンシティの戦い」のSジュンに書いてください：「世界の視点から見ると、人生は非常に大きい、個人的な観点からは人生は非常に小さいので、人生の意味は世界の人生からの個人の経験にある。私の人生から世界の人生を理解するための世界の人生は人生です。</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0018.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0018.md","frontmatter":{"title":"1月26日 水曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0018.md absPath of file >>> MarkdownRemark"}}

